.wrapper {
	padding-bottom: 20px;
	margin-bottom: 20px;
	display: flex;
	justify-content: space-around;
	column-gap: 15px;
	text-align: center;
	border-bottom: solid 1px rgb(255, 255, 255);

	& h3 {
		margin-bottom: 5px;
	}

	@media(max-width:500px) {
		& {
			font-size: 15px;
		}
	}

	@media(max-width:335px) {
		& {
			font-size: 12px;
		}
	}
}