.inspiration{display:flex;justify-content:center;align-items:center;font-family:"Garamond"}.inspiration button{padding:10px 35px 10px 10px;position:relative;display:block;font-size:18px;color:#ff3030;background:#025e9b;background:linear-gradient(90deg, rgb(37, 31, 143) 0%, rgb(0, 175, 210) var(--per-100));text-decoration:line-through;border-radius:10px;box-shadow:0 0 3px #04155e;user-select:none;transition:box-shadow .15s ease-in}@media(hover: hover){.inspiration button:hover{box-shadow:0 0 6px #04155e}}.inspiration button::after{content:"❌";position:absolute;top:50%;right:5px;transform:translateY(-50%)}.inspiration .is-active{color:rgba(60,255,0,.8);text-decoration:unset}.inspiration .is-active::after{content:"✅"}